* {
  padding: 0;
  margin: 0;
  font-family: "Ubuntu Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
}

/*shadows*/
.shadow {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

/*form*/

.form-container {
  width: 70vw !important;
  position: absolute;
  left: 0;
  top: 50px;
}

.form-container form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 25px;
}
.input-container {
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-top: 1rem;
}
label {
  font-size: 14px;
}

input,
.input {
  border: none;
  background-color: rgba(99, 99, 99, 0.05);
  border-radius: 5px;
  height: 35px;
  width: 100%;
  margin-top: 5px;
  padding-left: 30px;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 12px;
}

.checkbox {
  width: 15px;
  margin-right: 20px;
  background-color: red;
}

.submit-container {
  width: 100%;
  display: flex;
  justify-content: end;
  z-index: 2;
}

.submit-container button {
  height: 35px;
  font-weight: bold;
  background-color: #8fc760;
  color: white;
  border-radius: 5px;
  padding-inline: 10px;
  border: none;
  position: absolute;
  left: 0;
  bottom: 10px;
  width: 200px;
  z-index: 5;
  cursor: pointer;
}

/*page indicator*/

.page-indicator {
  position: fixed;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}

.indicator {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: rgba(99, 99, 99, 0.2);
  transition: all 0.3s ease;
}

.indicator.selected {
  height: 50px;
  border-radius: 25px;
  background-color: #8fc760;
}

