/* overlays and side menus*/
.side-overlay {
  width: 30%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: -60%;
  background-color: white;
  z-index: 99999999;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.side-menu-blur {
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(2px);
  position: fixed;
  z-index: 9999;
  top: 0 !important;
